import { gql, useQuery } from '@apollo/client'
import { Link, useParams } from 'react-router-dom'

const LAUNCH_QUERY = gql`
	query LaunchQuery($id: String!) {
		launch(id: $id) {
			id
			flight_number
			name
			date_utc
			date_local
			success
			rocket
		}
	}
`

const ROCKET_QUERY = gql`
	query RocketQuery($id: String!) {
		rocket(id: $id) {
			id
			name
			type
		}
	}
`

function Rocket({ id }) {
	const { loading, error, data } = useQuery(ROCKET_QUERY, { variables: { id } })
	if (loading) return <h4 className='mb-3'>Loading...</h4>
	if (error) return <h4 className='mb-3'>Error :(</h4>
	const { name, type } = data.rocket
	return (
		<>
			<h4 className='mb-3'>Rocket Details</h4>
			<ul className='list-group'>
				<li className='list-group-item'>Name: {name}</li>
				<li className='list-group-item'>Type: {type}</li>
			</ul>
		</>
	)
}
function Launch() {
	let { id } = useParams()
	const { loading, error, data } = useQuery(LAUNCH_QUERY, { variables: { id } })
	if (loading) return <h1 className='display-4 my-3'>Loading...</h1>
	if (error) return <h1 className='display-4 my-3'>Error :(</h1>
	const { flight_number, name, date_local, success, rocket } = data.launch
	return (
		<div>
			<h1 className='display-4 my-3'>
				<span className=''>Mission:</span> {name}
			</h1>
			<h4 className='mb-3'>Launch Details</h4>
			<ul className='list-group mb-3'>
				<li className='list-group-item'>Flight Number: {flight_number}</li>
				<li className='list-group-item'>Launch Year: {date_local}</li>
				<li className='list-group-item'>
					Launch Sucess:{' '}
					<span className={success ? 'text-success' : 'text-danger'}>
						{success ? 'Yes' : 'No'}
					</span>
				</li>
			</ul>
			<Rocket id={rocket} />
			<hr />
			<Link to='/' className='btn btn-secondary'>
				Back
			</Link>
		</div>
	)
}
export default Launch
